@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
:root{
    /* ligh-Theme */
    --VeryLightGray: hsl(0, 0%, 98%);
    --VeryLightGrayishBlue: hsl(236, 33%, 92%);
    --LightGrayishBlue: hsl(233, 11%, 84%);
    --DarkGrayishBlue: hsl(236, 9%, 61%);
    --VeryDarkGrayishBlue: hsl(235, 19%, 35%);

    /* dark-theme */
    --VeryDarkBlue: hsl(235, 21%, 11%);
    --VeryDarkDesaturatedBlueD: hsl(235, 24%, 19%);
    --LightGrayishBlueD: hsl(234, 39%, 85%);
    --LightGrayishBlueD2: hsl(236, 33%, 92%);
    --DarkGrayishBlue: hsl(234, 11%, 52%);
    --VeryDarkGrayishBlueD: hsl(233, 14%, 35%);
    --VeryDarkGrayishBlue: hsl(237, 14%, 26%);
}
html{
    box-sizing: border-box;
}
*,*::after,*::before{
    box-sizing: inherit;
    padding: 0;
    margin: 0;
    transition: 200ms ease-in-out;
}
body{
    font-family: 'Josefin Sans', sans-serif;
    min-height: 100vh;
    width: 100%;
}
.header_bg{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center bottom;
    height: 40vmin;
    width: 100%;
}
#Bg_L{
    background-image: url(/todo-app-react/static/media/bg-desktop-light.3508d620.jpg);
}
#Bg_D{
    background-image: url(/todo-app-react/static/media/bg-desktop-dark.73e47dbb.jpg);
}
.btn{
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-family: 'Josefin Sans', sans-serif;
    font-size: inherit;
}
.brand{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#brandName{
    color: hsl(0, 0%, 98%);
    font-size: 2.5rem;
    letter-spacing: 1.1rem
}
.Subbtn{
    height: 22px;
    width: 22px;
    cursor: pointer;
    border-radius: 50%;
    position: relative;

}
.addForm{
    display: flex;
    align-items: center;
    margin-top: 2rem;
    margin-bottom: 1rem;
    background-color: hsl(0, 0%, 98%);
    border-radius: 3px;
    padding: 0 1rem;
    box-shadow:
  0px 2.7px 10px rgba(0, 0, 0, 0.092),
  0px 25px 80px rgba(0, 0, 0, 0.19)
;

}
#B_D{
    border: 0.1px solid hsl(237, 14%, 26%);
    background-color: hsl(235, 24%, 19%);
}
#B_L{
    border: 0.1px solid hsl(233, 11%, 84%);
    background-color: hsl(0, 0%, 98%);
}
#addItems::-webkit-input-placeholder{
   font-family: 'Josefin Sans', sans-serif;
   color: hsl(236, 9%, 61%);
   font-size: 1.1rem;

}
#addItems:-ms-input-placeholder{
   font-family: 'Josefin Sans', sans-serif;
   color: hsl(236, 9%, 61%);
   font-size: 1.1rem;

}
#addItems::placeholder{
   font-family: 'Josefin Sans', sans-serif;
   color: hsl(236, 9%, 61%);
   font-size: 1.1rem;

}
#addItems{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 1.1rem;
}
.C_DD{
    color: hsl(0, 0%, 98%) ;
}
.C_LL{
    color: hsl(235, 24%, 19%);
}
#addItems:focus{
    outline: none;
}
#addItems{
    padding: 1rem ;
    width: 90%;
    border: none;
    background-color: transparent;

}
#Bg_DD{
    background-color: hsl(235, 24%, 19%);
}
.itemsLIst{
    max-width: 500px;
    box-shadow:
    0px 0.8px 3.6px rgba(0, 0, 0, 0.066),
    0px 2.7px 10px rgba(0, 0, 0, 0.095),
    0px 7.3px 24.1px rgba(0, 0, 0, 0.124),
    0px 25px 80px rgba(0, 0, 0, 0.19)
  ;  
  border-radius: 5px;
}
.item{
    width: 100%;
    padding: 1rem ;
    display: flex;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    font-size: 1.1rem;
}
#DBB{border-bottom: 1px solid hsla(233, 14%, 35%, 0.815)}
#LBB{border-bottom: 1px solid hsl(236, 33%, 92%);}

.delete{
    cursor: pointer;
    height: 18px;
    width: 18px;
    background-image: url(/todo-app-react/static/media/icon-cross.6ee81d30.svg);
    background-size: cover;
    background-position: center;
    border: none;
    background-color: transparent;
    margin-left: auto;
    display: none;
}
.text{
    font-size: 1.1rem;
}
.DList{
    background-color: hsl(235, 24%, 19%);
    color: hsl(0, 0%, 98%);
}
.LList{
    background-color: hsl(0, 0%, 98%);
    color: hsl(235, 24%, 19%);
   
}
.checkbox{
    display: none;
}
.checkbox:checked + .checkmark::after{
    display: flex;
}
.checkbox:checked ~ .delete{
    display: flex;
}
.checkmark{
    border: 0.1px solid var(--LightGrayishBlue);
    height: 22px;
    width: 22px;
    cursor: pointer;
    border-radius: 50%;
    position: relative;
}
.checkmark::after {
    content: url(/todo-app-react/static/media/icon-check.a8fb15d0.svg);
    background: linear-gradient(130deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
    height: 22px;
    width: 22px;
    position: absolute;
    top: -1px;
    left: -1px;
    display: none;
    margin: auto;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: 300ms ease-in-out;
}

.itemsLIst ul li:first-child{
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.itemsLIst ul{
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
footer{
    font-size: 0.9rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .8rem;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    position: relative;
}
footer menu{
    display: flex;
    grid-column-gap: .5rem;
    -webkit-column-gap: .5rem;
            column-gap: .5rem;
}
@media (max-width:500px){
    footer menu{
        position: absolute;
        bottom: -70px;
        width: 100%;
        left: 0;
        padding:  1rem;
        justify-content: center;
        border-radius: 3px;
        grid-column-gap: 1.5rem;
        -webkit-column-gap: 1.5rem;
                column-gap: 1.5rem;
        box-shadow:
        0px 2.7px 10px rgba(0, 0, 0, 0.092),
        0px 25px 80px rgba(0, 0, 0, 0.19)
      ;
    }
}
.container{
    max-width: 500px;

    margin: 0 auto;
    position: relative;
    height: 100%;
    -webkit-transform: translateY(-160px);
            transform: translateY(-160px);
}

@media (max-width:600px){
    .container{
        padding: 1rem;
        -webkit-transform: translateY(-110px);
                transform: translateY(-110px);
    }
}
